.custom_checkbox{
  input[type="checkbox"]{
    @apply hidden;
  }
  .decor{
    border-width: 3px;
    @apply w-6 h-6 border-textColor block cursor-pointer relative;
    
    svg{
      height: 0.9rem;
      width: 0.9rem;
      position: absolute;
      top: 52%;
      left: 50%;
      transform: translate(-50%, -50%) !important;

      @media (max-width: 767.98px){
        height: 0.7rem;
        width: 0.7rem;
      }
    }

    @media (max-width: 767.98px){
      @apply w-5 h-5;
    }
  }
}