.password-generator{
  width: 670px;
  &__title{
    @apply text-titleColor font-bold text-3xl text-center mb-10;

    @media (max-width: 767.98px){
      @apply text-xl mb-5;
    }
  }

  @media (max-width: 767.98px){
    width: 92%;
  }
}

.result{
  @apply bg-appColor relative p-7;

  input{
    @apply text-textColor font-semibold text-4xl;

    &::placeholder{
      @apply text-inputColor;
    }

    @media (max-width: 767.98px){
      @apply text-2xl
    }
  }
  
  &__button{
    @apply absolute top-1/2 right-7 -translate-y-1/2;
    svg{
      @apply w-9 h-9;

      @media (max-width: 767.98px){
        @apply w-7 h-7;
      }
    }
    svg *{
      @apply fill-buttonColor
    }

    @media (max-width: 767.98px){
      @apply right-4;
    }
  }

  @media (max-width: 767.98px){
    @apply p-4;
  }
}

.settings{
  @apply bg-appColor relative py-7 px-9 mt-6;
  font-size: 1.4rem;

  &__length{
    &-top{
      @apply flex justify-between items-center mb-5;

      @media (max-width: 767.98px){
        @apply mb-3;
      }
    }
    &-num{
      @apply text-buttonColor text-4xl;

      @media (max-width: 767.98px){
        @apply text-3xl;
      }
    }
  }

  &__checkboxes{
    @apply mt-10 flex flex-col gap-6;

    @media (max-width: 767.98px){
      @apply mt-5 gap-4;
    }
  }
  &__checkbox{
    @apply flex items-center gap-4;

    label{
      cursor: text;
    }

    @media (max-width: 767.98px){
      @apply gap-2;
    }
  }

  &__strength{
    @apply bg-darkGray text-inputColor py-6 px-7 font-bold mt-9 uppercase flex justify-between;

    &-rate{
      @apply flex gap-2;
      span{
        @apply h-full block w-3;
        border: 3px solid #fff;

        &.true{
          background-color: #fff;
        }

        @media (max-width: 767.98px){
          @apply w-3;
        }
      }
    }
    
    @media (max-width: 767.98px){
      @apply py-3 px-4 mt-5;
    }
  }
  
  &__button{
    @apply bg-buttonColor text-darkGray font-bold uppercase flex items-center justify-center py-5 mt-10 w-full gap-2;

    &:hover{
      @apply bg-opacity-90
    }
    @media (max-width: 767.98px){
      @apply py-3 mt-6;
    }
  }

  @media (max-width: 767.98px){
    @apply py-3 px-4 mt-6;
    font-size: 1.1rem;
  }
}


.alerts{
  @apply absolute left-1/2 bottom-7 flex flex-col gap-3;
  transform: translate(-50%, -0%);
}

.alert__item {
  @apply rounded-md text-lg font-semibold flex gap-4 items-center py-4 px-6;
  width: 500px;
  
  &-content{
    overflow-wrap: anywhere;
  }

  svg {
    @apply w-7 h-7 basis-7 flex-shrink-0;

    @media (max-width: 767.98px){
      @apply w-5 h-5;
    }
  }

  &.success{
    @apply bg-green-100 text-green-800;
    svg {
      @apply fill-green-500;
    }
  }

  &.error{
    @apply bg-red-100 text-red-800;
  }

  @media (max-width: 767.98px){
    width: 350px;
    @apply text-base py-3 px-4;
  }
}